import React, { useState } from "react";
import "./CommentModal.css"; // Import the CSS file

const CommentModal = ({ isOpen, onClose, onSubmit, postId }) => {
  const [nickname, setNickname] = useState("");
  const [comment, setComment] = useState("");
  const [password, setPassword] = useState("");
  // postId   
  console.log("Post ID in CommentModal:", postId);
  const handleSubmit = async () => {
    try {
      const requestBody = {
        nickname,
        content: comment,
        password,
      };

      const response = await fetch(`/api/posts/${postId}/comments`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        const errorData = await response.json();
        console.error("Error submitting comment:", errorData);
        alert("  .   .");
        return;
      }

      const responseData = await response.json();
      console.log("Comment submitted successfully:", responseData);

      alert("  !");
      //       .
      setNickname("");
      setComment("");
      setPassword("");

      onSubmit(responseData);
      onClose();
    } catch (error) {
      console.error("Error submitting comment:", error);
      alert("    .   .");
    }
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content-comment">
        <button className="comment-modal-close" onClick={onClose}>
          &times;
        </button>
        <h2 className="comment-title"> </h2>
        <form onSubmit={(e) => e.preventDefault()}>
          <div className="form-group-comment">
            <label htmlFor="nickname"></label>
            <input
              type="text"
              id="nickname"
              placeholder="  "
              value={nickname}
              onChange={(e) => setNickname(e.target.value)}
              required
            />
          </div>
          <div className="form-group-comment">
            <label htmlFor="comment"></label>
            <textarea
              id="comment"
              placeholder="  "
              value={comment}
              onChange={(e) => setComment(e.target.value)}
              required
            ></textarea>
          </div>
          <div className="form-group-comment">
            <label htmlFor="password"> </label>
            <input
              type="password"
              id="password"
              placeholder="   "
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <button
            type="submit"
            className="submit-button-comment"
            onClick={handleSubmit}
          >
            
          </button>
        </form>
      </div>
    </div>
  );
};

export default CommentModal;
