// ErrorModal.js
import React from "react";
import "./Modal.css"; //   

const ErrorModal = ({ message, onClose }) => {
  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2>  </h2>
        <p>{message}</p>
        <button onClick={onClose} className="modal-button">
          
        </button>
      </div>
    </div>
  );
};

export default ErrorModal;
