import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./CreateGroupPage.css"; //  
import Header from "../components/Header";
import SuccessModal from "../components/SuccessModal"; //   
import ErrorModal from "../components/ErrorModal"; //   
import axios from "axios";

function CreateGroupPage() {
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const [imageFile, setImageFile] = useState(null); //   
  const [isPublic, setIsPublic] = useState(true);
  const [introduction, setIntroduction] = useState("");
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [showErrorModal, setShowErrorModal] = useState(false);

  const navigate = useNavigate();
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    const maxSize = 5 * 1024 * 1024; // 5MB 
    const allowedTypes = ["image/jpeg", "image/png"];

    if (file.size > maxSize) {
      alert("  5MB    .");
      return;
    }

    if (!allowedTypes.includes(file.type)) {
      alert("JPEG  PNG     .");
      return;
    }

    setImageFile(file);
  };

  //   API 
  const uploadImage = async () => {
    const formData = new FormData();
    formData.append("image", imageFile);

    try {
      const response = await axios.post("/api/image", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });

      if (response.status === 200) {
        return response.data.imageUrl; //   imageUrl 
      } else {
        throw new Error("  .");
      }
    } catch (error) {
      console.error("  :", error);
      throw error;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("   ");

    try {
      //    
      const imageUrl = imageFile ? await uploadImage() : "";
      console.log("  :", imageUrl);

      const requestBody = {
        name,
        password,
        imageUrl, //   URL 
        isPublic,
        introduction,
      };

      const response = await axios.post("/api/groups", requestBody);
      console.log("  API :", response);

      if (response.status === 201) {
        setShowSuccessModal(true);
        console.log("  ");
        navigate("/");
      } else {
        setShowErrorModal(true);
        console.error("  :", response);
      }
    } catch (error) {
      console.error("    :", error);
      setShowErrorModal(true);
    }
  };

  const closeModal = () => {
    setShowSuccessModal(false);
    setShowErrorModal(false);
  };

  return (
    <div className="create-group-page">
      <div className="header-container">
        <Header />
      </div>
      <h1> </h1>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label> </label>
          <div className="image-upload">
            <input
              type="text"
              value={imageFile ? imageFile.name : "  "}
              readOnly
              className="file-input-display"
            />
            <input
              type="file"
              accept="image/*"
              onChange={handleImageChange}
              className="file-input"
              required
            />
            <button
              type="button"
              className="file-button"
              onClick={() =>
                document.querySelector('input[type="file"]').click()
              }
            >
               
            </button>
          </div>
        </div>
        <div className="form-group">
          <label> </label>
          <textarea
            placeholder="  "
            value={introduction}
            onChange={(e) => setIntroduction(e.target.value)}
            required
          ></textarea>
        </div>
        <div className="form-group">
          <label>  </label>
          <div className="switch-container">
            <span className="switch-label">{isPublic ? "" : ""}</span>
            <label className="switch">
              <input
                type="checkbox"
                checked={isPublic}
                onChange={(e) => setIsPublic(e.target.checked)}
              />
              <span className="slider round"></span>
            </label>
          </div>
        </div>

        <div className="form-group">
          <label> </label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="   "
            required
          />
        </div>
        <button type="submit" className="create-button">
          
        </button>
      </form>

      {showSuccessModal && (
        <SuccessModal
          message="  !"
          onClose={closeModal}
        />
      )}
      {showErrorModal && (
        <ErrorModal message="  ." onClose={closeModal} />
      )}
    </div>
  );
}

export default CreateGroupPage;
