import React, { useState, useEffect } from "react";
import axios from "axios";
import "./GroupEditModal.css";
import { useNavigate } from "react-router-dom";

const GroupEditModal = ({ isOpen, onClose, groupDetails, onSubmit }) => {
  const [formState, setFormState] = useState({
    name: "",
    image: "",
    description: "",
    isPublic: true,
    password: "",
  });
  const [imageFile, setImageFile] = useState(null);
  const navigate = useNavigate();

  //      useEffect
  useEffect(() => {
    if (groupDetails) {
      setFormState({
        name: groupDetails.name || "",
        image: groupDetails.image || "",
        description: groupDetails.description || "",
        isPublic:
          groupDetails.isPublic !== undefined ? groupDetails.isPublic : true,
        password: "",
      });
    }
  }, [groupDetails]);

  if (!isOpen) return null;

  //     
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    setImageFile(file);
  };

  //   
  const uploadImage = async () => {
    const formData = new FormData();
    formData.append("image", imageFile);

    try {
      const response = await axios.post("/api/image", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });

      if (response.status === 200) {
        return response.data.imageUrl;
      }
    } catch (error) {
      console.error("  :", error);
      throw new Error("  .");
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      let imageUrl = formState.image;
      if (imageFile) {
        imageUrl = await uploadImage();
      }

      const response = await axios.put(`/api/groups/${groupDetails.id}`, {
        name: formState.name,
        password: formState.password,
        imageUrl,
        isPublic: formState.isPublic,
        introduction: formState.description,
      });

      if (response.status === 200) {
        console.log("   ");
        onSubmit(response.data);
        onClose();
        navigate(`/groups/${groupDetails.id}`);
      }
    } catch (error) {
      console.error(
        "   :",
        error.response?.data?.message || error.message
      );

      if (error.response?.status === 403) {
        alert(" .");
      } else if (error.response?.status === 400) {
        alert(" .");
      } else if (error.response?.status === 404) {
        alert("  .");
      } else {
        alert(" .  .");
      }
    }
  };

  return (
    <div className="groupedit-modal-overlay">
      <div className="groupedit-modal-content">
        <button className="groupedit-modal-close" onClick={onClose}>
          &times;
        </button>
        <h2>  </h2>
        <form onSubmit={handleSubmit}>
          <div className="edit-form-group">
            <label></label>
            <input
              type="text"
              name="name"
              value={formState.name}
              onChange={(e) =>
                setFormState({ ...formState, name: e.target.value })
              }
              required
            />
          </div>
          <div className="edit-form-group">
            <label> </label>
            <div className="groupedit-image-upload">
              <input
                type="text"
                value={
                  imageFile
                    ? imageFile.name
                    : formState.image
                    ? " "
                    : "  "
                }
                readOnly
                className="groupedit-file-input-display"
              />
              <input
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                className="groupedit-file-input"
              />
              <button
                type="button"
                className="groupedit-file-button"
                onClick={() =>
                  document.querySelector(".groupedit-file-input").click()
                }
              >
                 
              </button>
            </div>
          </div>
          <div className="edit-form-group">
            <label> </label>
            <textarea
              name="description"
              value={formState.description}
              onChange={(e) =>
                setFormState({ ...formState, description: e.target.value })
              }
              placeholder="  "
            />
          </div>
          <div className="edit-form-group">
            <label>  </label>
            <div className="groupedit-switch-container">
              <span className="groupedit-switch-label">
                {formState.isPublic ? "" : ""}
              </span>
              <label className="groupedit-switch">
                <input
                  type="checkbox"
                  name="isPublic"
                  checked={formState.isPublic}
                  onChange={(e) =>
                    setFormState({ ...formState, isPublic: e.target.checked })
                  }
                />
                <span className="groupedit-slider round"></span>
              </label>
            </div>
          </div>

          <div className="edit-form-group">
            <label>  </label>
            <input
              type="password"
              name="password"
              value={formState.password}
              onChange={(e) =>
                setFormState({ ...formState, password: e.target.value })
              }
              placeholder="   "
              required
            />
          </div>
          <button type="submit" className="groupedit-create-button">
            
          </button>
        </form>
      </div>
    </div>
  );
};

export default GroupEditModal;
