import React, { useState } from "react";
import "./CommentDeleteModal.css";

const CommentDeleteModal = ({ isOpen, onClose, commentId, onDelete }) => {
  const [password, setPassword] = useState("");

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleDelete = async () => {
    try {
      const response = await fetch(`/api/comments/${commentId}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          password: password,
        }),
      });

      if (response.ok) {
        // const data = await response.json();
        alert("  .");
        onDelete(commentId);
        onClose();
      } else if (response.status === 400) {
        alert(" .");
      } else if (response.status === 403) {
        alert(" .");
      } else if (response.status === 404) {
        alert("  .");
      } else {
        alert("  .");
      }
    } catch (error) {
      console.error("Error deleting comment:", error);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content-delete">
        <button className="close-button" onClick={onClose}>
          &times;
        </button>
        <h2 className="delete-page-title"> </h2>
        <div className="delete-form-group">
          <label className="delete-label">  </label>
          <input
            type="password"
            value={password}
            onChange={handlePasswordChange}
            placeholder="   "
            className="delete-password-input"
            required
          />
        </div>
        <button onClick={handleDelete} className="delete-submit-button">
          
        </button>
      </div>
    </div>
  );
};

export default CommentDeleteModal;
