import React, { useState } from "react";
import axios from "axios"; // axios  
import "./MemoryDeleteModal.css";

const MemoryDeleteModal = ({
  isOpen,
  onClose,
  onDelete = () => {},
  postId,
}) => {
  const [password, setPassword] = useState("");

  if (!isOpen) return null;

  const handleDelete = async () => {
    try {
      //   API 
      const response = await axios.delete(`/api/posts/${postId}`, {
        data: { postPassword: password }, //    
      });

      if (response.status === 200) {
        //   
        console.log("  ");
        onDelete(); //      (:   )
        onClose(); //   
      }
    } catch (error) {
      //   
      console.error(
        "  :",
        error.response?.data?.message || error.message
      );

      if (error.response?.status === 403) {
        alert(" .");
      } else if (error.response?.status === 400) {
        alert(" .");
      } else if (error.response?.status === 404) {
        alert("  .");
      } else {
        alert(" .  .");
      }
    }
  };

  return (
    <div className="modal-overlay">
      <div className="memorydelete-modal-content">
        <button className="close-button" onClick={onClose}>
          &times;
        </button>
        <h2> </h2>
        <div className="memorydelete-form-group">
          <label>  </label>
          <input
            type="password"
            placeholder="   "
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button className="memorydelete-button" onClick={handleDelete}>
          
        </button>
      </div>
    </div>
  );
};

export default MemoryDeleteModal;
