import React, { useState, useEffect } from "react";
import "./UploadMemory.css";
import axios from "axios";
import Header from "../components/Header";
import { useNavigate, useParams } from "react-router-dom";
import xIcon from "../assets/icon=x.svg";
import UploadModal from "../components/UploadModal";

const UploadMemory = (onClose) => {
  const [nickname, setNickname] = useState("");
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [postPassword, setPostPassword] = useState("");
  const [imageFile, setImageFile] = useState(null);
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState("");
  const [location, setLocation] = useState("");
  const [moment, setMoment] = useState("");
  const [isPublic, setIsPublic] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const navigate = useNavigate();
  const { groupId } = useParams();

  // Check if groupId is correctly retrieved
  useEffect(() => {
    console.log("groupId:", groupId); // groupId   
    if (!groupId) {
      alert("   ID.");
      navigate("/"); //  ID    
    }
  }, [groupId, navigate]);

  const handleTagChange = (event) => {
    setTagInput(event.target.value);
  };

  const handleTagKeyDown = (event) => {
    if (event.key === "Enter" && tagInput.trim() !== "") {
      event.preventDefault();
      setTags([...tags, tagInput.trim()]);
      setTagInput("");
    }
  };

  const handleRemoveTag = (indexToRemove) => {
    setTags(tags.filter((_, index) => index !== indexToRemove));
  };

  const handleImageChange = (event) => {
    setImageFile(event.target.files[0]);
  };

  //   
  const uploadImage = async () => {
    const formData = new FormData();
    formData.append("image", imageFile);

    try {
      const response = await axios.post("/api/image", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });

      if (response.status === 200) {
        return response.data.imageUrl;
      }
    } catch (error) {
      console.error("  :", error);
      throw new Error("  .");
    }
  };

  const handleModalSubmit = async (groupPassword) => {
    try {
      let imageUrl = "";

      //    
      if (imageFile) {
        try {
          imageUrl = await uploadImage();
        } catch (error) {
          console.error("  :", error);
          alert("  .");
          return; //       
        }
      }

      const requestBody = {
        nickname,
        title,
        content,
        postPassword,
        groupPassword,
        imageUrl,
        tags,
        location,
        moment,
        isPublic,
      };

      // groupId    
      if (!groupId) {
        alert("   ID.");
        return;
      }

      // groupId     
      const response = await axios.post(
        `/api/groups/${groupId}/posts`,
        requestBody
      );

      if (response.status === 200) {
        alert("  .");
        navigate(`/groups/${groupId}`);
      }
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  };

  return (
    <div className="upload-memory-page">
      <Header />

      <h1 className="page-title"> </h1>
      <button className="close-button" onClick={onClose}>
        &times;
      </button>
      <form className="memory-form" onSubmit={(e) => e.preventDefault()}>
        <div className="form-section">
          <div className="form-group">
            <label></label>
            <input
              type="text"
              value={nickname}
              onChange={(e) => setNickname(e.target.value)}
              placeholder="  "
              required
            />
          </div>

          <div className="form-group">
            <label></label>
            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="  "
              required
            />
          </div>

          <div className="form-group">
            <label></label>
            <div className="image-upload">
              <input
                type="text"
                value={imageFile ? imageFile.name : "  "}
                readOnly
                className="file-input-display"
              />
              <input
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                className="file-input"
                required
              />
              <button
                type="button"
                className="file-button"
                onClick={() =>
                  document.querySelector('input[type="file"]').click()
                }
              >
                 
              </button>
            </div>
          </div>

          <div className="form-group">
            <label></label>
            <textarea
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="   "
              required
            />
          </div>
        </div>

        <div className="divider"></div>

        <div className="form-section">
          <div className="form-group">
            <label></label>
            <input
              type="text"
              value={tagInput}
              onChange={handleTagChange}
              onKeyDown={handleTagKeyDown}
              placeholder="  "
              className="tags-input"
            />
            <div className="tags-list">
              {tags.map((tag, index) => (
                <div key={index} className="tag-item">
                  #{tag}{" "}
                  <img
                    src={xIcon}
                    alt="remove tag"
                    className="tag-remove"
                    onClick={() => handleRemoveTag(index)}
                  />
                </div>
              ))}
            </div>
          </div>

          <div className="form-group">
            <label></label>
            <input
              type="text"
              value={location}
              onChange={(e) => setLocation(e.target.value)}
              placeholder="  "
            />
          </div>

          <div className="form-group">
            <label> </label>
            <input
              type="date"
              value={moment ? new Date(moment).toISOString().split("T")[0] : ""}
              onChange={(e) => setMoment(e.target.value)}
            />
          </div>

          <div className="form-group">
            <label>  </label>
            <div className="switch-container">
              <span className="switch-label">
                {isPublic ? "" : ""}
              </span>
              <label className="switch">
                <input
                  type="checkbox"
                  checked={isPublic}
                  onChange={(e) => setIsPublic(e.target.checked)}
                />
                <span className="slider round"></span>
              </label>
            </div>
          </div>

          <div className="form-group">
            <label> </label>
            <input
              type="password"
              value={postPassword}
              onChange={(e) => setPostPassword(e.target.value)}
              placeholder="   "
              required
            />
          </div>
        </div>

        <button
          type="submit"
          className="submit-button"
          onClick={() => setIsModalOpen(true)}
        >
          
        </button>
      </form>
      <UploadModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSubmit={handleModalSubmit}
      />
    </div>
  );
};

export default UploadMemory;
